import React from 'react';
import style from './App.scss';
/**
 *  
 */
class AutoScalingText extends React.Component {

    constructor(props){
        super(props);
        this.state = {
            scale: 1  //
        };
    }

    /**
     * propsstateDOM
     * render
     */
    componentDidUpdate() {
        const { scale } = this.state;
        const node = this.refs.textNode;
        const parentNode = node.parentNode;

        /**
         * actualWidthavailableWidthactualWidthavailableWidth
         * scale
         */
        const availableWidth = parentNode.offsetWidth;
        const actualWidth = node.offsetWidth;
        const actualScale = availableWidth / actualWidth;  //11

        /**
         *  actualScale<1setStateactualScalethis.props.scale
         *  componentDidUpdate,
         *   scale === actualScale 
         */
        if (scale === actualScale){
            return ;
        }

        if (actualScale < 1) {
            this.setState({ scale: actualScale })
        } else if (scale < 1) {
            this.setState({ scale: 1 })
        }
    }


    render() {

        const { scale } = this.state;

        /**
         *  transformscale(x,y) x,y 
         */
        let divProps = {
            className: style['auto-scaling-text'],
            style: {transform: `scale(${scale},${scale})`},
            ref: 'textNode'
        };
        return (
            <div {...divProps}>
                {this.props.children}
            </div>
        )
    }
}

export default AutoScalingText;


